import axios from 'axios'
import { Toast } from 'we-vue'

export default {
  baseUrl: "",
  request: function(_method, _url, _reqdata){
    _reqdata = _reqdata || {};

    var config = {
      method: _method,
      url: _url,
      baseURL:this.baseUrl,
      withCredentials: true,
      timeout: 30000,
    };

    if(/^http[s]?:\/\//.test(_url)){
      config.baseURL = '';
    }

    switch(_method.toLowerCase()){
      case 'get':
        config.params = _reqdata;
        break;
      case 'post':
      case 'put':
      case 'patch':
        config.data = _reqdata;
        break;
      default:
        console.log("http.request")
        break;
    }

    return new Promise(function(resolve, reject){
      axios(config).then(function(resData){
        let result = resData.data;
		let status = resData.status;
        let MESSAGE = null;
        switch (status) {
          case 200:
            MESSAGE = null;
            break;
          case 401:
            window.location.href = "/login";
            break;
		  case 404:
            MESSAGE = "";
            break;
          default:
            break;
        }

        if(MESSAGE){
          Toast.fail({duration: 2000, message: MESSAGE, icon: 'warn'})
        }
		if(resData.status == 200){
		  resolve(result);
		}
      }).catch(function(error){
        Toast.fail({duration: 2000, message: "", icon: 'warn'})
        reject(error);
      });
    });
  },
  get: function(_url, _reqdata){
    return this.request("get", _url, _reqdata);
  },
  post: function(_url, _reqdata){
    return this.request("post", _url, _reqdata);
  }
}



