import http from "./http"

let api = {
  get_info:"/api/wcup/list_current/",      //
  get_old_info:"/api/wcup/list_history/",     //
  set_cup:"/api/wcup/support_country/",  //
  get_ranking:"/api/wcup/ranking/",  //
  login:"/api/account/login/",    //
  logout:"/api/account/logout/",  //
  userinfo:"/api/account/user_info/",
  get_weixin:"/api/account/authen/" // codeuserId deviceId
}

let httpApi = {
  get_info: function(){return http.get(api.get_info)},
  get_old_info: function(){return http.get(api.get_old_info)},
  set_cup: function(d){return http.post(api.set_cup, d)},
  get_ranking:function(){return http.get(api.get_ranking)},
  login:function(d){return http.post(api.login, d)},
  logout:function(){return http.get(api.logout)},
  userinfo:function(){return http.get(api.userinfo)},
  get_weixin:function(d){return http.post(api.get_weixin, d)}
}
export default {
  httpApi
}



